import React, { Component } from 'react';
import { Table, Divider, Tag, Button, Input, Modal } from 'antd';
import axios from 'axios';
import fetchJsonp from 'fetch-jsonp';


const columns = [{
  title: '设备编号',
  dataIndex: 'sbbh',
  key: 'sbbh',
},{
  title: '设备名称',
  dataIndex: 'sbmc',
  key: 'sbmc',
}, {
  title: '设备型号',
  dataIndex: 'sbxh',
  key: 'sbxh',
}, {
  title: 'IP地址',
  dataIndex: 'ipdz',
  key: 'ipdz',
}, {
  title: '端口号',
  dataIndex: 'dk',
  key: 'dk',
},{
  title: '安装时间',
  dataIndex: 'azrqsm',
  key: 'azrqsm',
},{
  title: '管理机构',
  dataIndex: 'jgmc',
  key: 'jgmc',
},{
  title: '制造商',
  dataIndex: 'zzssm',
  key: 'zzssm',
},{
  title: '供应商',
  dataIndex: 'gyssm',
  key: 'gyssm',
},{
  title: '操作',
  key: 'action',
  render: (text, record) => (
    <span>
      <Tag color="green">Update {record.name}</Tag>
      <Divider type="vertical" />
      <Tag color="red">Delete</Tag>
    </span>
  ),
}];
class SignalSafeguard extends Component{
  constructor(props){
    super(props)
    this.state = {
      ListAxiosData: [],
      ListFetchJsonpData: [],
      dataSource: [],//getUsers
      visible: false,
      title: "修改",
    }
  }
  getAxiosData = () => {
    let api = 'http://www.phonegap100.com/appapi.php?a=getPortalList&catid=20';   //接口后台允许了跨域
    axios.get(api)
      .then((response) => {
        console.log(response.data.result);
        this.setState({
          ListAxiosData: response.data.result,
        })
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  getFetchJsonpData = () => {
    let api="http://www.phonegap100.com/appapi.php?a=getPortalList&catid=20";
    fetchJsonp(api)
      .then((response) => {
        return response.json()
      }).then((json) => {
        this.setState({
          ListFetchJsonpData: json.result
        })
      }).catch(function(ex) {
        console.log(ex)
      })
  }
  componentWillMount(){
    // let url = 'http://61.50.100.22:60000/bana_fvs/code/getCodes.do';//user/getUsers.do';//function/getFunctions.do'
    let url = 'http://10.1.1.109:8080/bana_oms/device/getDevices.do';//读取设备
    axios.post(url, {
      // gnbm: '01010000',
      // gnmc: '雾区监测',
      token: 'YWRtaW58MHy53MDt1LF8MTgwMTIzNDU2Nzh8MHzWo9bdytC5q7Cyvta9u82ovq%2By7NanttN8MTU0NDUwOTc4MjkxNw%3D%3D',
      data: {
        pageNum: 1,
        pageSize: 10,
      }
    })
    .then((response) => {
      console.log(response.data);
      this.setState({
        dataSource: response.data.datas
      })
    })
    .catch(function (error) {
      console.log(error);
    });

  }
  showModal = () => {
    this.setState({
      visible: true,
    });
  }

  handleOk = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }

  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  }
  render(){
    return(
      <div className="SignalSafeguard main">
        <div className="searchBox">
          <ul>
            <li className="searchList">
              <label>设备编号:</label>
              <Input placeholder="请输入" />
            </li>
            <li className="searchList">
              <label>设备名称:</label>
              <Input placeholder="请输入" />
            </li>
            <li className="searchList">
              <label>管理机构:</label>
              <Input placeholder="请输入" />
            </li>
            <li className="searchBtn">
            <Button type="primary" icon="search">查询</Button>
            </li>
          </ul>
        </div>
        <div className="btnBox">
          <Button type="primary" onClick={this.showModal}>Open Modal</Button>
          <Button type="primary" onClick={this.getAxiosData} style={{marginLeft:'5px'}}>axios</Button>
          <Button type="primary" onClick={this.getFetchJsonpData} style={{marginLeft:'5px'}}>fetch-jsonp</Button>
          <div className="listBox" style={{background: '#fff',marginTop: '10px',overflow: 'auto'}}>
            <ul style={{float: 'left'}}>
              {
                this.state.ListAxiosData.map((value,key)=>{
                  return <li key={key}>{value.aid}</li>
                })
              }
            </ul>
            <ul style={{float: 'left',marginLeft: '20px'}}>
              {
                this.state.ListFetchJsonpData.map((value,key)=>{
                  return <li key={key}>{value.title}</li>
                })
              }
            </ul>
          </div>
        </div>
        <div className="gridBox">
          <Table dataSource={this.state.dataSource} columns={columns} rowKey="sbbh"/>
        </div>
        <Modal
          title={this.state.title}
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <p>Some contents...</p>
          <p>Some contents...</p>
          <p>Some contents...</p>
        </Modal>
      </div>
    )
  }
}

export default SignalSafeguard;
